<template>
<Popup :mask="true" position="bottom" :maskClosable="false" :visible="isShow" type="schedule-popup" @mask-click="$emit('on-hide')">
  <div class="name-notice-page">
    <div class="header border-bottom-1px">
      <span class="font-18"></span>
      <img class="close-icon" @click="$emit('on-hide')" :src="closeIcon" />
    </div>
    <div class="notice-content">
      <p>1.</p>
      <br/>
      <p>2.</p>
      <br/>
      <p>3.</p>
      <br/>
      <p>4.301230</p>
      <br/>
      <p>5...</p>
    </div>
  </div>
</Popup>
</template>
<script>
import { Popup } from 'cube-ui'
import closeIcon from '@/assets/images/close@3x.png'

export default {
  name: 'NameNotice',
  components: {
    Popup,
  },
  data() {
    return {
      closeIcon
    }
  },
  props: {
    isShow: {
      type: Boolean,
      default: false
    },
  },
  computed: {},
  created() {},
  mounted() {},
  beforeDestroy() {},
  destroyed() {},
  watch: {},
  methods: {}
}
</script>
<style scoped lang="less">
@import "~assets/styles/var.less";
  .name-notice-page{
    width: 100%;
    min-height: @drawer-height-min;
    max-height: @drawer-height-max;
    background-color: #FFFFFF;
    display: flex;
    flex-direction: column;
    .header {
      height: 5.04rem;
      line-height: 5.04rem;
      text-align: center;
      position: relative;
      flex: 0 0 5.04rem;
      .close-icon {
        height: 1.333rem;
        width: 1.333rem;
        position: absolute;
        right: 1.25rem;
        top: 0;
        bottom: 0;
        margin: auto;
      }
    }
    .notice-content{
      font-size: 14px;
      color: #666666;
      line-height: 24px;
      margin-top: 1rem;
      text-align: left;
      flex: 1 1;
      padding: 1.33rem 1.33rem 1.17rem;
      overflow-y: scroll;
    }
  }
</style>



